#header {
    display: block;
    min-width: 1180px;
    background: black;
    z-index: 10;
    width: 100%;
    position: fixed;
    top: 0;

}

#header *:hover {
    transition: all 0.20s ease-out;
}

#header .btn_togglearea {
    position: absolute;
    right: 0px;
    top: 18px;
    width: 45px;
    height: 45px;
    cursor: pointer;
    z-index: 1;
}

#header .btn_togglearea {
}

#header .btn_togglearea:hover #btn_toggle:before {
    display: block;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 1px;
    margin: 0;
    background: rgba(187, 241, 243, .3);
    border-bottom-right-radius: 50px;
    z-index: 0;
}

#header #btn_toggle {
    display: block;
    position: relative;
    width: 20px;
    margin: 0;
    padding: 13px;
    cursor: pointer;
}

#header #btn_toggle span {
    display: block;
    width: 100%;
    height: 3px;
    line-height: 0;
    background: #1e1e1e;
    border-radius: 3px;
    transition: .8s ease;
}

#header #btn_toggle span.b {
    width: 65%;
}

#header #btn_toggle span.c {
    width: 80%;
}

#header #btn_toggle .b {
    margin: 5px 0;
}

#header #btn_toggle:hover span {
    width: 100%;
}

#header #btn_toggle:hover .a {
    -webkit-transform: translateX(5px);
    transform: translateX(5px);
}

#header #btn_toggle:hover .b {
    -webkit-transform: translateX(-5px);
    transform: translateX(-5px);
}

#header #btn_toggle:hover .c {
    -webkit-transform: translateX(5px);
    transform: translateX(5px);
}

